body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-size: 30px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.highlight {
  background: #9832ff54;
}
/* .hideLetter { */
/* color: yellow; */
/* } */

.duplicate {
  position: absolute;
  top: 20%;
  font-family: -webkit-pictograph;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 59px;
  -webkit-font-feature-settings: none;
          font-feature-settings: none;
  -webkit-font-variant-ligatures: none;
          font-variant-ligatures: none;
  letter-spacing: 4px;
  /* min-width: 1068px; */
}

.keyboard {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);

  width: 100%;
  min-width: 1068px;
}

.keyboard > div:nth-child(5) {
  /* hide space bar line */
  /* display: none; */
}

.button {
  border-color: rgb(216, 216, 216) rgb(209, 209, 209) rgb(186, 186, 186);
  border-style: solid;
  border-width: 1px;
  border-radius: 10px;

  padding: 1px 7px 2px;
  font-size: 29px;

  margin: 5.1px 3.1px;
  width: 35.8px;
  height: 43px;
}

.special_button {
  font-size: 15px;
}

.pressed {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.line {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.line .button:nth-child(1) {
  background: lightgrey;
}
.line .button:nth-child(2) {
  background: pink;
}
.line .button:nth-child(3) {
  background: teal;
}
.line .button:nth-child(4) {
  background: paleturquoise;
}
.line .button:nth-child(5) {
  background: sandybrown;
}
.line .button:nth-child(6) {
  background: sandybrown;
}
.line .button:nth-child(7) {
  background: cornflowerblue;
}
.line .button:nth-child(8) {
  background: cornflowerblue;
}
.line .button:nth-child(9) {
  background: paleturquoise;
}
.line .button:nth-child(10) {
  background: teal;
}
.line .button:nth-child(11) {
  background: pink;
}
.line .button:nth-child(12) {
  background: pink;
}
.line .button:nth-child(13) {
  background: pink;
}
.line .button:nth-child(14) {
  background: lightgrey;
}

.tab {
  width: 60px;
}
.caplock {
  width: 77px;
}
.restart {
  width: 77px;
}
.shift {
  width: 105px;
}
.space {
  width: 305px;
}
.alt {
  width: 45px;
}

